import { collection, getDocs } from 'firebase/firestore';
import React from 'react'
import { useState, useEffect } from 'react';
import { db } from '../firebase/config';
import { Swiper, SwiperSlide } from "swiper/react";
import { EventsData } from "../Data/events"
// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

import "./eventsStyle.css";

// import required modules
import { Pagination } from "swiper";
import Heading from '../constants/heading';


const Events = () => {
    const [Data, setData] = useState([]);


    useEffect(() => {
        const getCourses = async () => {
            let List = [];
            const querySnapshot = await getDocs(collection(db, "events"));
            querySnapshot.forEach((doc) => {
                List.push({ ...doc.data(), id: doc.id })
                setData(List);
            })
        }

        getCourses()

    }, [])



    return (
        <div>
            <div className="event-row">
                <Heading
                    hText="Events"
                    hPara="Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis"
                />

                <Swiper
                    breakpoints={{
                        300: {
                            width: 420,
                            slidesPerView: 1,
                        },
                        450: {
                            width: 480,
                            slidesPerView: 1,
                            centeredSlides: false,
                            centeredSlidesBounds: false
                        },
                        600: {
                            width: 580,
                            slidesPerView: 2,
                            centeredSlides: false,
                            centeredSlidesBounds: false

                        },
                        768: {
                            width: 750,
                            slidesPerView: 2,
                            centeredSlides: false,
                            centeredSlidesBounds: false
                        },

                        1000: {
                            width: 1000,
                            slidesPerView: 3,
                            centeredSlides: false,
                            centeredSlidesBounds: false
                        },
                    }}
                    slidesPerView={3}
                    spaceBetween={30}
                    modules={[Pagination]}
                    className="mySwiperEvent"
                    loop={true}
                >
                    {
                        EventsData.map((data) => (
                            <SwiperSlide key={data.id} className='event-slide'>
                                <div className="event-col">
                                    <div className="event-img">
                                        <img src={data.img} alt="" />
                                    </div>
                                    <div className="event-content">
                                        <h3>{data.type}</h3>
                                        <p>{data.text}</p>
                                    </div>
                                    <div className="event-footer">
                                        <div className="event-date">
                                            <i className="bx bx-calendar"></i> {data.Date}
                                        </div>
                                        <a href=''>Read Article <i className="bx bx-chevron-right"></i></a>
                                    </div>
                                </div>
                            </SwiperSlide>
                        ))
                    }

                </Swiper>
            </div>
        </div>
    )
}

export default Events;