import React from 'react'
import cover from "../images/bnr1.jpg";
import "./heroStyle.css";
import { Swiper, SwiperSlide } from "swiper/react";
import { useRef, useState, useEffect } from 'react';



// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";

import "./heroStyle.css";

const Hero = () => {
    const swiperRef = useRef();

    return (
        <div className='hero-section' >
            <div className="hero-slider">
                <Swiper
                    navigation={false}
                    onBeforeInit={(swiper) => {
                        swiperRef.current = swiper;
                    }}

                    className="mySwiperHero" >
                    <SwiperSlide className='hero-slide'>
                        <div className="hero-img">
                            <img src={cover} alt="" />
                            <div className="hero-layer">
                                <h2 className='mb-4'>Welcome to Sayan University</h2>
                                <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry It was popularised in the 1960s with the release </p>
                                <div className="buttons d-flex">
                                    <a href="" style={{cursor: 'pointer'}} className='btn py-2'>Start The Journey <i className="bx bx-chevron-right"></i></a>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>
                    <div className="nav-buttons">
                        <div className="prev-btn" onClick={() => swiperRef.current?.slidePrev()}><i className="bx bx-chevron-left mx-5"></i></div>
                        <div className="next-btn" onClick={() => swiperRef.current?.slideNext()}><i className="bx bx-chevron-right mx-5"></i></div>
                    </div>
                </Swiper>
            </div>
            <div className="hero-categs">
                <div className="hero-cat-row">
                    <div className="hero-cat-col">
                        <div className="hero-cat-layer"></div>
                        <i className="bx bxs-school icon"></i>
                        <h4>Lifelong Learning</h4>
                        <a href=''>Read More <i className="bx bx-chevron-right"></i></a>
                    </div>
                    <div className="hero-cat-col">
                        <div className="hero-cat-layer"></div>
                        <i className="bx bxs-graduation icon"></i>
                        <h4>Graduate Education</h4>
                        <a href=''>Read More <i className="bx bx-chevron-right"></i></a>
                    </div>
                    <div className="hero-cat-col">
                        <div className="hero-cat-layer"></div>
                        <i className="bx bx-underline icon"></i>
                        <h4>Undergraduate Education</h4>
                        <a href=''>Read More <i className="bx bx-chevron-right"></i></a>
                    </div>
                    <div className="hero-cat-col">
                        <div className="hero-cat-layer"></div>
                        <i className="bx bx-run icon"></i>
                        <h4>Center Atheltics Life</h4>
                        <a href=''>Read More <i className="bx bx-chevron-right"></i></a>
                    </div>
                </div>
            </div>

        </div>

    )
}

export default Hero;