import React from 'react'
import Heading from '../constants/heading';
import { Swiper, SwiperSlide } from "swiper/react";
import { useState, useEffect } from 'react';




// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

import "./careStyle.css";

// import required modules
import { Pagination } from "swiper";
import { collection, getDocs } from 'firebase/firestore';
import { db } from '../firebase/config';
import { Health } from '../Data/health';


const HealthCare = () => {
    const [data, setdata] = useState([]);



    useEffect(() => {
        const getArticles = async () => {
            let List = [];
            const querySnapshot = await getDocs(collection(db, "healthcare"));
            querySnapshot.forEach((doc) => {
                List.push({ ...doc.data(), id: doc.id });
                setdata(List);
            });
        }
        getArticles()


    }, [])

    return (
        <div className='care-row'>
            <Heading
                hText="Health Care"
                hPara="Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis"
            />
            <Swiper
                breakpoints={{
                    300: {
                        width: 420,
                        slidesPerView: 1,
                    },
                    450: {
                        width: 480,
                        slidesPerView: 1,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },
                    600: {
                        width: 550,
                        slidesPerView: 2,
                        centeredSlides: false,
                        centeredSlidesBounds: false

                    },
                    768: {
                        width: 720,
                        slidesPerView: 2,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },

                    1000: {
                        width: 1000,
                        slidesPerView: 3,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },
                }}
                slidesPerView={3}
                spaceBetween={30}
                centeredSlides={true}
                centeredSlidesBounds={true}
                modules={[Pagination]}
                className="mySwiperCare"
            >


                {
                    Health.map((data) => (
                        <SwiperSlide key={data.id} className='care-slide'>
                            <div className="care-col">
                                <div className="care-img">
                                    <img src={data.img} alt="" />
                                </div>
                                <div className="care-layer">
                                    <p>{data.type} </p>
                                    <a href="">Read More on Healthcare <i className="bx bx-chevron-right"></i></a>
                                </div>
                            </div>
                        </SwiperSlide>
                    ))
                }

            </Swiper>

        </div>
    )
}

export default HealthCare;