import { collection, getDocs } from 'firebase/firestore';
import React from 'react'
import { useState, useEffect } from 'react';
import { db } from '../firebase/config';
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "./courseStyle.css";
import { useSelector, useDispatch } from "react-redux";

import { Pagination } from "swiper";
import Heading from '../constants/heading';
import { Courses } from '../Data/courses';


const Course = () => {
    const [Data, setData] = useState([]);


    useEffect(() => {

        const getCourses = async () => {
            let List = [];
            const querySnapshot = await getDocs(collection(db, "Courses"));
            querySnapshot.forEach((doc) => {
                List.push({ ...doc.data(), id: doc.id })
                setData(List);
            })
        }

        getCourses()

    }, [])


    const handlClick = (id) => {
        window.localStorage.setItem('blog_id', id);
    }



    return (
        <div>
            <div className="course-row">
                <Heading
                    hText="Our Courses"
                    hPara="Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis"
                />


                <Swiper
                    breakpoints={{
                        300: {
                            width: 420,
                            slidesPerView: 1,
                        },
                        450: {
                            width: 480,
                            slidesPerView: 1,
                        },
                        640: {
                            width: 590,
                            slidesPerView: 2,
                        },
                        768: {
                            width: 768,
                            slidesPerView: 2,
                        },
                    }}
                    slidesPerView={3}
                    spaceBetween={30}

                    modules={[Pagination]}
                    className="mySwiperCourse"
                    loop={false}
                >
                    {
                        Courses.map((data) => (
                            <SwiperSlide key={data.id} className='course-slide'>
                                <div className="course-col">
                                    <div className="course-img">
                                        <img src={data.img} alt="" />
                                    </div>
                                    <div className="course-flex">
                                        <p><i className="bx bx-user"></i> 321 </p>
                                        <p> <i className="bx bx-chat"></i> 3.2k</p>
                                    </div>
                                    <div className="course-content">
                                        <h3 style={{ cursor: 'pointer' }} onClick={() => { handlClick(data.id) }}>{data.title}</h3>
                                        <div className="prof-info">
                                            <div className="prof-img">
                                                <img src={data.teacherIMG} alt="" />
                                            </div>
                                            <div className="prof-text">
                                                <p >With <span className='teacher-name'>{data.teacherName}</span></p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="course-footer">
                                        <div className="course-rating">
                                            <span className="bx bxs-star"></span>
                                            <span className="bx bxs-star"></span>
                                            <span className="bx bxs-star"></span>
                                            <span className="bx bxs-star"></span>
                                            <span className="bx bxs-star-half"></span>
                                        </div>
                                        <p>Free</p>
                                    </div>
                                </div>
                            </SwiperSlide>
                        ))
                    }

                </Swiper>
            </div>
        </div>
    )
}

export default Course;