import React from 'react'
import cover from "../images/cover2.jpg";
import { Link } from 'react-router-dom';
import "./subHeroStyle.css";

const SubHero = (props) => {
    return (
        <div className='subhero-section'>
            <div className="subhero-row">
                <div className="subhero-col">
                    <div className="subhero-img">
                        <img src={cover} alt="" />
                    </div>
                    <div className="subhero-layer">
                        <h1>{props.page}</h1>
                        <div className="subhero-links">
                            <Link className='sub-link'>Home</Link>
                            /
                            <p>{props.pathPage}</p>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default SubHero;