import React from 'react'
import Heading from '../constants/heading';
import { Swiper, SwiperSlide } from "swiper/react";
import { useState, useEffect } from 'react';




// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

import "./centerLifeStyle.css";

// import required modules
import { Pagination } from "swiper";
import { collection, getDocs } from 'firebase/firestore';
import { db } from '../firebase/config';
import { Center } from '../Data/center';


const CenterLife = () => {
    const [data, setdata] = useState([]);



    useEffect(() => {
        const getArticles = async () => {
            let List = [];
            const querySnapshot = await getDocs(collection(db, "articls"));
            querySnapshot.forEach((doc) => {
                List.push({ ...doc.data(), id: doc.id });
                setdata(List);
            });
        }
        getArticles()


    }, [])

    return (
        <div className='center-life-row'>
            <Heading
                hText="Center Life"
                hPara="Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis"
            />
            <Swiper

                breakpoints={{
                    300: {
                        width: 420,
                        slidesPerView: 1,
                    },
                    450: {
                        width: 500,
                        slidesPerView: 1,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },
                    600: {
                        width: 580,
                        slidesPerView: 2,
                        centeredSlides: false,
                        centeredSlidesBounds: false

                    },
                    768: {
                        width: 780,
                        slidesPerView: 2,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },

                    1000: {
                        width: 1150,
                        slidesPerView: 3,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },
                }}
                slidesPerView={3}
                spaceBetween={30}
                centeredSlides={true}
                centeredSlidesBounds={true}
                modules={[Pagination]}
                className="mySwiperCenter"
                loop={true}

            >


                {
                    Center.map((data) => (
                        <SwiperSlide key={data.id} className='center-slide'>
                            <div className="center-col">
                                <div className="center-img">
                                    <img src={data.img} alt="" />
                                </div>
                                <div className="center-layer">
                                    <p>{data.type} </p>
                                    <p>{data.text} </p>
                                </div>
                            </div>
                        </SwiperSlide>
                    ))
                }

            </Swiper>

        </div>
    )
}

export default CenterLife;