import { collection, getDocs } from "firebase/firestore";
import Heading from "../constants/heading";
import "./newsStyle.css";
import React from 'react'
import { useState, useEffect } from "react";
import { db } from "../firebase/config";
import { NewsItems } from "../Data/news";


const News = () => {
    const [data, setdata] = useState([]);


    useEffect(() => {

    }, [])


    return (
        <div className="news-section">
            <Heading
                hText="Latest News"
                hPara="Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis"
            />
            <div className="news-row">
                <div className="news-col left">
                    <div className="nl-row">
                        {
                            NewsItems.slice(0, 2).map((data) => (
                                <div key={data.id} className="nl-col">
                                    <div className="nl-img">
                                        <img src={data.img} alt="" />
                                    </div>
                                    <div className="nl-content">
                                        <div className="nl-flex">
                                            <p><i className="bx bx-user"></i> By Admin</p>
                                            <p><i className="bx bx-bookmark-alt"></i> Social Sciences</p>
                                        </div>
                                        <h5>{data.title}</h5>
                                        <a href="">Read More <i className="bx bx-chevron-right"></i></a>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
                <div className="news-col right">
                    <div className="nr-row">
                        {
                            NewsItems.slice(2, 3).map((data) => (
                                <div key={data.id} className="nr-col">
                                    <div className="nr-img">
                                        <img src={data.img} alt="" />
                                    </div>
                                    <div className="nr-content">
                                        <div className="nr-flex">
                                        <p><i className="bx bx-user"></i> By Admin</p>
                                            <p><i className="bx bx-bookmark-alt"></i> Social Sciences</p>
                                        </div>
                                        <h5>{data.title}</h5>
                                        <a href="">Read More <i className="bx bx-chevron-right"></i></a>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}

export default News;