import React from 'react'
import Heading from '../constants/heading';
import { Swiper, SwiperSlide } from "swiper/react";
import { useState, useEffect } from 'react';




// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

import "./storiesStyle.css";

// import required modules
import { Pagination } from "swiper";
import { collection, getDocs } from 'firebase/firestore';
import { db } from '../firebase/config';


const Stories = () => {
    const [data, setdata] = useState([]);



    useEffect(() => {
        const getStories = async () => {
            let List = [];
            const querySnapshot = await getDocs(collection(db, "stories"));
            querySnapshot.forEach((doc) => {
                List.push({ ...doc.data(), id: doc.id });
                setdata(List);
            });
        }
        getStories()


    }, [])

    return (
        <div className='story-row'>
            <Heading
                hText="Our Students Stories"
                hPara="Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis"
            />
            <Swiper
                breakpoints={{
                    300: {
                        width: 420,
                        slidesPerView: 1,
                    },
                    450: {
                        width: 480,
                        slidesPerView: 1,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },
                    600: {
                        width: 580,
                        slidesPerView: 2,
                        centeredSlides: false,
                        centeredSlidesBounds: false

                    },
                    768: {
                        width: 750,
                        slidesPerView: 2,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },

                    1000: {
                        width: 1100,
                        slidesPerView: 3,
                        centeredSlides: false,
                        centeredSlidesBounds: false
                    },
                }}
                slidesPerView={3}
                spaceBetween={30}
                centeredSlides={true}
                centeredSlidesBounds={true}
                pagination={{
                    clickable: true,
                }}
                modules={[Pagination]}
                className="mySwiperStories"

            >


                <SwiperSlide key={data.id} className='story-slide'>
                    <div className="story-col">
                        <h5>{data.title} </h5>
                        <iframe width="356" height="234" src="https://www.youtube.com/embed/jIHs1al0CCs" title="XXXTENTACION - Riot (Remix) ft. 2Pac, The Notorious B.I.G." frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
                    </div>
                </SwiperSlide>

                <SwiperSlide key={data.id} className='story-slide'>
                    <div className="story-col">
                        <h5>{data.title} </h5>
                        <iframe width="356" height="234" src="https://www.youtube.com/embed/w7xZl0N9ey8" title="       web development" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
                    </div>
                </SwiperSlide>

                <SwiperSlide key={data.id} className='story-slide'>
                    <div className="story-col">
                        <h5>{data.title} </h5>
                        <iframe width="365" height="234" src="https://www.youtube.com/embed/E4veOfnLu0E" title="     -   -     " frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
                    </div>
                </SwiperSlide>
            </Swiper>

        </div>
    )
}

export default Stories;