import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    value: 0,
    prodToView: null
}

const counterSlice = createSlice({
    name: 'count',
    initialState,
    reducers: {
        Prod_To_View(state, action) {
            state.prodToView = action.payload;
            // console.log(action.payload);
        }
    }
});

export const { Prod_To_View } = counterSlice.actions

export default counterSlice.reducer;