import React from 'react'
import Heading from '../constants/heading';
import { useState, useEffect } from 'react';
import "./podcastStyle.css";
import { PodcastItems } from '../Data/podcast';


const Podcast = () => {
    const [data, setdata] = useState([]);
    const [filterList, setfilterList] = useState([])


    useEffect(() => {
        setdata(PodcastItems);
        let list = [... new Set(PodcastItems.map((item)=> item.type))]
        setfilterList(list);
    }, [])

    const getCat = (item) => {
        let filter = PodcastItems.filter((data) => data.type === item);
        setdata(filter);
    }

    return (
        <div className='podcast-section'>
            <div className="podcast-grids">
                <div className="podcast-grid-col left">
                    <Heading
                        hText="Our Podcast"
                        hPara="Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis"
                    />
                    <div className="podcast-row">
                        {
                            data.slice(0, 4).map((data) => (
                                <div key={data.id} className="podcast-col">
                                    <div className="podcast-img">
                                        <img src={data.img} alt="" />
                                        <div className="podcast-layer">
                                            <p className='pod-epi'>Podcast: {data.episode}</p>
                                            <div className="pod-play">
                                                <i className="bx bx-play">
                                                    <div className="pod-play-layer"></div>
                                                </i>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="podcast-content">
                                        <h3>{data.text} </h3>
                                        <p>{data.Date} </p>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
                <div className="podcast-grid-col right">
                    <div className="podcast-filter">
                        <div className="card border-0">
                            <div className="card-header border-0">
                                <h3>Categories</h3>
                            </div>
                            <div className="card-body">
                                {
                                    filterList.map((filter, i) => (
                                        <p key={i} onClick={() => { getCat(filter) }}><i className="bx bx-headphone bx-flashing"></i> {filter}</p>
                                    ))
                                }
                            </div>
                        </div>
                        <div className="card form-container mt-5">
                            <div className="card-body form-body">
                                <i className="bx bx-envelope"></i>
                                <div className="form-header">
                                    <h3>Subscribe To Newsletter</h3>
                                    <p>Get Updates of News and Events</p>
                                </div>
                                <div className="form-layer"></div>
                                <form action="">
                                    <div className="from-group">
                                        <input type="text" placeholder='Your Email Address' className='form-control py-3 my-2' name="" id="" />
                                        <button className="btn btn-danger w-100 py-3 mt-3">Subscribe</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Podcast;