import React from 'react'
import Navbar from '../../components/navbar';
import SubHero from '../../constants/subHero';
import { useState, useEffect } from 'react';
import { collection, getDocs } from "firebase/firestore";
import { db } from '../../firebase/config';
import "./centerStyle.css";
import Heading from '../../constants/heading';
import CountUp from 'react-countup';
import Footer from '../../components/footer';


const Center = () => {
    const [data, setdata] = useState([]);
    const [Reached, setReached] = useState(false);
    const [data2, setdata2] = useState([]);

    const scroll = () => {
        if (window.pageYOffset != "undefined") {
            return window.pageYOffset;
        }
    }


    window.addEventListener('scroll', () => {
        let scrolled = scroll();
        if (scrolled > 750) {
            setReached(true);
        } else {
            setReached(false)
        }
    })
    useEffect(() => {
        const getData = async () => {
            let list = [];
            const querySnapshot = await getDocs(collection(db, "articls"));
            querySnapshot.forEach((doc) => {
                list.push({ id: doc.id, ...doc.data() });
            });
            setdata(list);

        }

        const getData2 = async () => {
            let list2 = [];
            const querySnapshot = await getDocs(collection(db, "students life"));
            querySnapshot.forEach((doc) => {
                list2.push({ id: doc.id, ...doc.data() });
            });
            setdata2(list2);

        }

        getData2();
        getData();
    }, [])


    return (
        <div className='center-p-section'>
            <Navbar />
            <SubHero
                pathPage="Center Life"
                page='Center Life'
            />

            <div className="center-p-row">
                {
                    data.map((data) => (
                        <div key={data.id} className="center-p-col">
                            <div className="center-p-img">
                                <img src={data.img} alt="" />
                            </div>
                            <div className="center-p-content">
                                <p>{data.type} </p>
                                <h3>{data.text} </h3>
                            </div>
                        </div>
                    ))
                }
            </div>



            <div className="counter-container">
                <Heading
                    hText='Center Life'
                    hPara='Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis'
                />
                <div className="counter-row">
                    <div className="counter-col">
                        <div className="count-flex">
                            <h2>
                                {Reached && <CountUp start={0} end={20} duration={8} />} K
                            </h2>
                            <p>+</p>
                        </div>
                        <div className="count-text">
                            <p>Total Students</p>
                        </div>
                    </div>
                    <div className="counter-col">
                        <div className="count-flex">
                            <h2>
                                {Reached && <CountUp start={0} end={52} duration={8} />}
                            </h2>
                            <p>+</p>
                        </div>
                        <div className="count-text">
                            <p>students Nationalities</p>
                        </div>
                    </div>
                    <div className="counter-col">
                        <div className="count-flex">
                            <h2>
                                {Reached && <CountUp start={0} end={15} duration={8} />}
                            </h2>
                            <p>+</p>
                        </div>
                        <div className="count-text">
                            <p>Years Of Experience</p>
                        </div>
                    </div>
                    <div className="counter-col">
                        <div className="count-flex">
                            <h2>
                                {Reached && <CountUp start={0} end={70} duration={8} />}
                            </h2>
                            <p>%</p>
                        </div>
                        <div className="count-text">
                            <p>Objectif Reach</p>
                        </div>
                    </div>
                    <div className="counter-col">
                        <div className="count-flex">
                            <h2>
                                {Reached && <CountUp start={0} end={12} duration={8} />}
                            </h2>
                            <p>+</p>
                        </div>
                        <div className="count-text">
                            <p>Different Majors </p>
                        </div>
                    </div>
                </div>
            </div>
            <div className="sl-container">
                <Heading
                    hText='Students Life'
                    hPara="Lorem ipsum dolor sit amet consectetur adipiscing elit ut elit tellus luctus nec ullamcorper mattis"
                />
                <div className="sl-row pb-5 mb-5">
                    {
                        data2.slice(0, 1).map((data) => (
                            <div className="sl-col-left">
                                <div className="sl-img">
                                    <img src={data.image} alt="" />
                                    <h3>{data.text}</h3>
                                </div>
                            </div>
                        ))
                    }
                    <div className="sl-col-right">
                        {
                            data2.slice(1, 3).map((data, i) => (
                                <div className="sl-col-wrapper">
                                    <div className="sl-img">
                                        <img src={data.image} alt="" />
                                        <h3>{data.text}</h3>
                                    </div>
                                </div>
                            ))
                        }
                    </div>

                </div>
            </div>
            <Footer />
        </div>
    )
}

export default Center;